.App {
  text-align: center;
}

.App-header {
  background-image: radial-gradient(ellipse at center,#f8f8f8 0,#bbbbd1 100%);
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  overflow: hidden;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
