.container {
    display: flex;
    height: 50vh;
    justify-content: center; 
}

.header {
    font-size: 2rem;
}

@media (max-width: 1080px)  {
    .container {
        margin-left: 0.75rem;
    }
}

.content-wrapper {
    display: flex;
    text-align: left;
    flex-flow: column;
    justify-content: space-between;
    min-width: 290px;
    z-index: 1;
}

.name {
    margin-top: 1rem;
    font-weight: bold;
    font-size: 2.5rem;
}

.nav-container {
    font-size: small;
    margin-top: 1rem;
    font-weight: bold;
    text-decoration: underline;
}

.nav-container > * {
    margin-left: 0.5rem;
}

@media (max-width: 1080px)  {
    .nav-container > :last-child {
        margin-top: 0.25rem;
    }
    .nav-container {
        margin-right: 0.5rem;
    }
}
@media (max-width: 720px)  {
    .nav-container > * {
        margin-top: 0.25rem;
        margin-left: 0;
    }
    .nav-container {
        display: flex;
        flex-flow: column;
        align-items: center;
    }
}

.sub-content {
    font-size: medium;
    font-style: italic;
}

.button {
    box-shadow: 0 0.125rem 0.25rem rgba(0,0,0,.16);
    border: 1px solid #91a6ad;
    background-color: #fff;
    color: #000000;
    padding: .5rem .5rem;
    cursor: pointer;
    border-radius: .25rem;
    font-weight: bold;
    max-width: 9rem;
    justify-content: center;
}

.logo {
    height: 40vmin;
    pointer-events: none;
    filter: blur(4px);
  }

  .react-wrapper {
    float: left;
    transform: scale(1.8);
  }

  .gastby-wrapper {
    float: right;
    transform: scale(0.8);
    margin-top: -300px;
  }
  .gql-wrapper {
    margin-top: 150px;
  }